@font-face {
    font-family: 'FontAwesome';
    src: url('../fonts/fontawesome-webfont.eot?v=4.7.0');
    src: url('../fonts/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('../fonts/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('../fonts/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('../fonts/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('../fonts/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@import 'owl.carousel.min.css';
@import 'owl.theme.default.css';
* {
    margin: 0;
    padding: 0;
}

.wrapper {
    width: 1200px;
    margin: 0 auto;
}

.header-top {
    padding: 15px;
    display: flex;
    justify-content: space-between;
}

.header-botton {
    padding: 15px;
    display: flex;
    justify-content: space-between;
    ul {
        font-weight: bold;
        padding-top: 13px;
        display: flex;
        font-size: 11px;
        text-align: right;
    }
    li {
        padding-right: 26px;
        color: #494e62;
        padding-left: 5px;
        letter-spacing: 0.29px;
        list-style: none;
    }
}

.visitca {
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    .owl-carousel {
        .owl-nav {
            position: absolute;
            bottom: 10%;
            left: 10%;
            .owl-next {
                span {
                    width: 42px;
                }
            }
        }
        .item {
            position: relative;
            img {
                width: 100vw;
            }
            .item-discription {
                position: absolute;
                width: 500px;
                height: 240px;
                left: 22vw;
                top: 24%;
                h4 {
                    font-family: Commissioner;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 40px;
                    line-height: 50px;
                    letter-spacing: 0.01em;
                    color: rgba(0, 0, 0, 0.9);
                }
                p {
                    font-family: Lato;
                    font-style: normal;
                    font-weight: 300;
                    font-size: 15px;
                    line-height: 25px;
                    color: rgba(0, 0, 0, 0.8);
                    padding: 20px 0 50px 0;
                }
                button {
                    width: 200px;
                    height: 45px;
                    background: #DEC377;
                    border-radius: 2px;
                    font-family: Commissioner;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 13px;
                    line-height: 30px;
                    text-align: center;
                    letter-spacing: 0.08em;
                    text-transform: uppercase;
                    color: #FFFFFF;
                    border: none;
                }
            }
        }
    }
}

.basic-information {
    background: #E5E5E5;
    h2 {
        font-family: Commissioner;
        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        line-height: 40px;
        letter-spacing: 0.03em;
        color: rgba(0, 0, 0, 0.9);
    }
    .quality-mark {
        position: relative;
        top: -50px;
        z-index: 100;
        img {
            width: 270px;
        }
    }
    .legal-technology {
        background-color: #fff;
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
        margin-bottom: 100px;
        .legal-technology_item {
            width: 539px;
            padding: 50px 70px;
            p {
                padding: 30px 70px 0 0;
                font-family: Lato;
                font-style: normal;
                font-weight: 300;
                font-size: 15px;
                line-height: 25px;
                color: rgba(0, 0, 0, 0.8);
            }
        }
    }
    .main-directions {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .main-directions_item {
            position: relative;
            width: 370px;
            background: #FFFFFF;
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
            margin: 30px 30px 30px 0;
            div {
                position: relative;
                width: 180px;
                height: 90px;
                margin: 25px 40px;
                h3 {
                    font-family: Commissioner;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 20px;
                    line-height: 30px;
                    color: rgba(0, 0, 0, 0.9);
                }
                p {
                    font-family: Lato;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 20px;
                    letter-spacing: 0.02em;
                    text-transform: uppercase;
                    color: rgba(0, 0, 0, 0.5);
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    padding-top: 10px;
                    cursor: pointer;
                }
            }
            img {
                position: absolute;
                top: 15px;
                right: 15px;
            }
        }
    }
    .btn-allServuse {
        width: 200px;
        height: 45px;
        background: #DEC377;
        border-radius: 2px;
        font-family: Commissioner;
        font-style: normal;
        font-weight: bold;
        font-size: 13px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #FFFFFF;
        border: none;
        cursor: pointer;
    }
    .discription {
        padding-top: 100px;
        display: flex;
        justify-content: space-between;
        p {
            font-family: Lato;
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 25px;
            color: rgba(0, 0, 0, 0.8);
            width: 64%;
        }
    }
    .gallery {
        position: relative;
        bottom: -60px;
        margin-bottom: 60px;
        .owl-carousel {
            .owl-nav {
                .owl-prev,
                .owl-next {
                    position: absolute;
                    bottom: 50%;
                }
                .owl-prev {
                    left: -3%;
                }
                .owl-next {
                    right: -3%;
                }
            }
        }
    }
}

.show {
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
}

.reviews {
    h2 {
        font-family: Commissioner;
        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        line-height: 40px;
        letter-spacing: 0.03em;
        color: rgba(0, 0, 0, 0.9);
        padding: 100px 0 35px 0;
    }
    .owl-carousel {
        position: relative;
        .owl-stage-outer {
            padding-bottom: 30px;
            padding-left: 20px;
        }
        .owl-nav {
            position: absolute;
            bottom: -30px;
            right: 0;
        }
        .owl-dots {
            position: absolute;
            bottom: -30px;
            right: 46%;
        }
        .item {
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.08);
            height: 550px;
            width: 570px;
            div {
                padding: 0 50px 65px 50px;
            }
            span {
                font-family: Lato;
                font-style: normal;
                font-weight: normal;
                font-size: 15px;
                line-height: 25px;
                color: rgba(0, 0, 0, 0.5);
            }
            img {
                padding-bottom: 40px;
            }
            p {
                font-family: Lato;
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                line-height: 25px;
                letter-spacing: 0.01em;
                color: rgba(0, 0, 0, 0.8);
                padding-bottom: 25px;
            }
            button {
                font-family: Lato;
                font-style: normal;
                font-weight: 600;
                font-size: 12px;
                line-height: 20px;
                letter-spacing: 0.02em;
                text-transform: uppercase;
                color: rgba(0, 0, 0, 0.5);
                border: none;
                cursor: pointer;
                background-color: transparent;
                position: absolute;
                bottom: 65px;
            }
            h4 {
                font-family: Commissioner;
                font-style: normal;
                font-weight: bold;
                font-size: 26px;
                line-height: 40px;
                letter-spacing: 0.01em;
                color: #DEC377;
                padding-bottom: 20px;
            }
        }
    }
    .btn-allServuse {
        width: 200px;
        height: 45px;
        background: #DEC377;
        border-radius: 2px;
        font-family: Commissioner;
        font-style: normal;
        font-weight: bold;
        font-size: 13px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #FFFFFF;
        border: none;
        cursor: pointer;
    }
}

@media screen and (max-width: 390px) {}
